import React from "react";
import classNames from "classnames";
import { GatsbyImage, getImage } from "gatsby-plugin-image"

export const Certification: React.FunctionComponent<any> = React.memo((props: any) => {
    const {
        csm,
        csd,
        cspo,
        mvp
    } = props

    return <div className={classNames("row")}>
        <div className={classNames("col")}>
            <GatsbyImage
                className={classNames("img-fluid", "w-100")}
                image={getImage(csm)}
                alt="CSM"/>
        </div>
        <div className={classNames("col")}>
            <GatsbyImage
                className={classNames("img-fluid", "w-100")}
                image={getImage(csd)}
                alt="CSD"/>
        </div>
        <div className={classNames("col")}>
            <GatsbyImage
                className={classNames("img-fluid", "w-100")}
                image={getImage(cspo)}
                alt="CSPO"/>
        </div>
        <div className={classNames('col', "d-flex", 'align-items-center')}>
            <GatsbyImage
                className={classNames("img-fluid", "w-100")}
                image={getImage(mvp)}
                alt="MVP"/>
        </div>
    </div>
});
