import { library } from "@fortawesome/fontawesome-svg-core";
import { fab } from "@fortawesome/free-brands-svg-icons";
import { fas } from "@fortawesome/free-solid-svg-icons";
import classNames from "classnames";
import { graphql } from "gatsby";
import { GatsbyImage, getImage } from "gatsby-plugin-image"
import React from "react"
import { Certification } from "../component/resume/certification";
import ResumeExperiences from "../component/resume/experiences";
import ResumeProfile from "../component/resume/profile";
import SEO from "../component/seo";

const Resume: React.FunctionComponent<any> = (props: any) => {
    library.add(fas, fab);
    const {
        configYaml: profile,
        allMdx: { edges: exps },
        my
    }: any = props.data

    return <>
        <SEO title={ "Bruno Jan Resume" } description={ props.description }/>
        <section className={ classNames('g-layout-boxed', 'g-bgi-v8') }>
            <main className={ classNames('container') }>
                <div className={ classNames('row', 'justify-content-between', 'g-pt-100') }>
                    <div className={ classNames('col-12') }>
                        <div className={ classNames('g-brd-around', 'g-brd-gray-light-v4', 'g-pa-12', 'g-mb-40') }>
                            <div className={ 'row' }>
                                <div className={ classNames('col-4') }>
                                    <div className={ 'g-py-30' }>
                                        <GatsbyImage
                                            className={ classNames('img-fluid', 'w-100') }
                                            image={ getImage(my) }
                                            alt="Bruno Jan"/>
                                    </div>
                                </div>
                                <div className={ classNames('col-8') }>
                                    <ResumeProfile { ...profile } />
                                    <hr className={ classNames('g-brd-gray-light-v4', 'g-my-20') }/>
                                    <Certification { ...props.data } />
                                </div>
                            </div>
                        </div>
                    </div>
                    <div className={ classNames('col-12') }>
                        <ResumeExperiences exps={ exps }/>
                    </div>
                </div>
            </main>
        </section>
    </>
}

export const pageQuery = graphql`
    query {
        my: imageSharp(fluid: {originalName: {eq: "myimage.jpg"}}) {
            gatsbyImageData(width: 400)
        }
        csm: imageSharp(fluid: {originalName: {eq: "csm.png"}}) {
            gatsbyImageData(width: 400)
        }
        csd: imageSharp(fluid: {originalName: {eq: "csd.png"}}) {
            gatsbyImageData(width: 400)
        }
        cspo: imageSharp(fluid: {originalName: {eq: "cspo.png"}}) {
            gatsbyImageData(width: 400)
        }
        mvp: imageSharp(fluid: {originalName: {eq: "mvp.png"}}) {
            gatsbyImageData(width: 400)
        }
        configYaml {
            name
            about
            birthday
            education
            email
            job_title
            links {
                url
                title
                icon {
                    iconName
                    prefix
                }
            }
        }
        allMdx(filter: {frontmatter: {category: {eq: "experiences"}}}, sort: {fields: frontmatter___during, order: DESC}) {
            edges {
                node {
                    frontmatter {
                        company
                        title
                        during
                        english
                    }
                    body
                }
            }
        }
    }
`

export default Resume;
