import { faBriefcase } from "@fortawesome/free-solid-svg-icons";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { MDXProvider } from "@mdx-js/react";
import classNames from "classnames";
import { MDXRenderer } from "gatsby-plugin-mdx";
import React from "react";
import { connponents } from "./index";

const WorkExperience: React.FunctionComponent = (props: any) => {
    const {
        frontmatter: {
            company,
            title,
            during,
            english
        },
        body
    } = props;
    return <li className={ classNames("col-12",
                                      "g-brd-gray-light-v4",
                                      "animate__animated",
                                      "animate__fadeInDown") }>
        <div className={ classNames("row") }>
            <div className={ classNames("col-4",
                                       "g-py-15",
                                       "text-align-end",
                                       'g-pr-30',
                                       'g-brd-right',
                                       "g-brd-gray-light-v4") }>
                <h4 className={ classNames("h4", "g-font-weight-300") }>{ company }</h4>
                <h4 className={ classNames("h4", "g-font-weight-300") }>{ english }</h4>
                <h5 className={ classNames("h5", "g-font-weight-300") }>{ title }</h5>
                <h6 className={ classNames("h6", "g-font-weight-300", "mb-0") }>{ during }</h6>
            </div>
            <div className={ classNames("col-8",
                                        "align-self-center",
                                        "g-brd-darkblue-left",
                                        "g-brd-5",
                                        'g-pos-rel') }>
                <div className={ classNames('g-left-minus-10', 'g-pos-abs', "g-top-35") }>
                    <i className={ classNames("d-block",
                                              "g-width-18",
                                              "g-height-18",
                                              "g-bg-purple",
                                              "g-brd-around",
                                              "g-brd-4",
                                              "g-brd-gray-light-v4",
                                              "rounded-circle") }/>
                </div>
                <article className={ classNames("g-pb-50", 'g-px-20') }>
                    <MDXProvider components={ connponents }>
                        <MDXRenderer>{ body }</MDXRenderer>
                    </MDXProvider>
                </article>
            </div>
        </div>
    </li>
};

const ResumeExperiences: React.FunctionComponent<any> = (props: any) => {
    const { exps } = props;
    return <div className={ classNames("card", "border-0", "g-mb-40") }>
        <div className={ classNames("u-heading-v3", "g-mb-40", 'g-mx-20') }>
            <h2 className={ classNames("h2", "u-heading-v3__title") }>
                <FontAwesomeIcon width={ 14 } icon={ faBriefcase }
                                 className={ classNames("g-pos-rel", "g-top-1", "g-mr-5") }/>
                工作經歷
            </h2>
        </div>

        <div
            className={ classNames("u-info-v1-1", "g-bg-white-gradient-v1--after", "g-pa-0") }>
            <div className={ classNames("c-scrollbar", "g-pb-40") }>
                <ul className={ classNames("row", "list-unstyled") }>
                    {
                        exps.map((item: any, index: number) => {
                            return <WorkExperience { ...item.node } key={ index }/>;
                        })
                    }
                </ul>
            </div>

        </div>
    </div>
}

export default ResumeExperiences;
